<template>
  <div id="app">
    <el-row>
      <el-col :span="3">
        <div class="grid-content bg-purple">
          <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
            <el-radio-button :label="false">展开</el-radio-button>
            <el-radio-button :label="true">收起</el-radio-button>
          </el-radio-group>
          <el-menu default-active="1-4-1" 
                    class="el-menu-vertical-demo" 
                    @open="handleOpen" 
                    @close="handleClose" 
                    :collapse="isCollapse">
            <el-submenu index="1" :disabled=ifDisabled.disabled1>
              <template slot="title">
                <i class="el-icon-setting"></i>
                <span slot="title">系统管理</span>
              </template>
              <el-menu-item-group>
                <span slot="title">管理分组</span>
                <el-menu-item index="1-1" @click="function(){skip('/user/user');}" >用户管理</el-menu-item>
                <el-menu-item index="1-2" @click="function(){skip('/user/authority');}" >权限管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item index="2" @click="function(){skip('/student');}" :disabled=ifDisabled.disabled2>
              <i class="el-icon-star-off"></i>
              <span slot="title">学员管理</span>
            </el-menu-item>
            <el-menu-item index="3" @click="function(){skip('/schedule');}" :disabled=ifDisabled.disabled3>
              <i class="el-icon-tickets"></i>
              <span slot="title">教案管理</span>
            </el-menu-item>
            <el-menu-item index="4" @click="function(){skip('/calendar');}" :disabled=ifDisabled.disabled4>
              <i class="el-icon-date"></i>
              <span slot="title">进度排期</span>
            </el-menu-item>
            <el-menu-item index="5" @click="function(){skip('/calendar');}" :disabled=ifDisabled.disabled5>
              <i class="el-icon-menu"></i>
              <span slot="title">辅助功能</span>
            </el-menu-item>
          </el-menu>
        </div>
      </el-col>
      <el-col :span="21"><div class="grid-content bg-purple-dark">
        <router-view/>
      </div></el-col>
    </el-row>

    <!-- <img src="./assets/logo.png"> -->
    
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isCollapse: true,
      ifDisabled:{
        disabled1:false,
        disabled2:false,
        disabled3:false,
        disabled4:false,
        disabled5:true
      }
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    skip(val){
      location.hash = val;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>



