<template>
  <div class="user">
    <el-row>
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-edit" circle></el-button>
      </el-col>
    </el-row>

    <el-row style="margin-top: 10px;">
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          

          <el-collapse>
            <el-collapse-item title="管理面板" name="1">
              <el-row>
                <el-col :span="10">
                  <el-form :inline="true" :model="formInline" class="demo-form-inline">

                    <el-form-item label="用户名">
                      <el-input placeholder="用户名"></el-input>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="primary">查询</el-button>
                    </el-form-item>
                  </el-form>
                </el-col>
              </el-row>


            </el-collapse-item>
          </el-collapse>

        </div>
      </el-col>
    </el-row>

    <el-row style="margin-top: 10px;">
      <el-col :span="24">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%">
          <el-table-column
            prop="date"
            label="用户名">
          </el-table-column>
          <el-table-column
            prop="name"
            label="密码">
          </el-table-column>
          
          <el-table-column
            prop="name"
            label="操作"
            width="280">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'user',
  data () {
    return {
    }
  },
  methods: {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


