<template>
  <div class="calendar">

    <vue-event-calendar :events="demoEvents" 
                        @month-changed="monthChange" 
                        @day-changed="dayChange">

      <template scope="props">
        
        <div v-for="(event, index) in props.showEvents" class="event-item">

          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{event.title}}</span>
              <el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>
            </div>
            <div class="text item">
              {{event.desc}}
            </div>
          </el-card>
        </div>

      </template>
    </vue-event-calendar>
  </div>
</template>

<script>
export default {
  name: 'calendar',
  data () {
    return {
      demoEvents: [{
        date: '2018/07/15',
        title: 'eat',
        desc: 'longlonglong description'
      },{
        date: '2018/07/16',
        title: '你叫',
        desc: 'longlonglong description'
      },{
        date: '2018/07/17',
        title: '你叫',
        desc: 'longlonglong description'
      },{
        date: '2018/07/18',
        title: '你叫',
        desc: 'longlonglong description'
      },{
        date: '2018/07/19',
        title: '你叫',
        desc: 'longlonglong description'
      }]
    }
  }
  ,
  methods: {
    monthChange (month) {
      console.log(month)
    },
    dayChange (day) {
      console.log(day)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


